import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';

import AccountForm from '../components/AccountForm';
import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';

export default class Users extends Component {
    constructor(props) {
        super(props);
        window.userListActionEdit = this.edit;
        this.dataTable = React.createRef();
        this.dataTableConfig = {
            serverSide: true,
            processing: true,
            ajax: function(params, callback, settings) {
                axios.get('/api/account/users', {params: {params}}).then(response=>{
                    callback(response.data);
                })
            },
            columns: [
                { data: 'firstname', name: 'firstname', 'class': 'text-center'},
                { data: 'lastname', name: 'lastname', 'class': 'text-center'},
                { data: 'email', name: 'email', 'class': 'text-center'},
                { data: 'phonenumber', name: 'phonenumber', 'class': 'text-center'},
                { data: null, 'class': 'text-center', render: function(data,type,row) {
                    return `<span>
                      <a onclick="userListActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
                  </span>`
                }}
            ],
        };
    }

    edit = (user) => {
        this.props.history.push(`${this.props.match.url}/${user}`);
    }

	render() {
		let match = this.props.match;
		return <div className="container-fluid">
    	    <div className="row">
        	    <div className="col-md-12">
        	      <span className="pull-right">
        	        <Link to={`${match.url}/new`} className="btn btn-success details">Add User</Link>
        	      </span>
        	    </div>
    	    </div>
            <DataTable ref={this.dataTable} title="Users" config={this.dataTableConfig}>
                <th>Firstname</th>
                <th>Lastname</th>
                <th>Email</th>
                <th>Phonenumber</th>
                <th className="disabled-sorting text-center noExport">Actions</th>
            </DataTable>
		</div>

	}
}

export class UserNew extends Component {
    state = { error: {} };
    submit = (data) => {
        return axios.post('/api/account/users', data).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            error = error.response ? error.response.data : error;
            this.setState({error});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push('/school/account/users');
    }
    render() {
        return <AccountForm error={this.state.error} submit={this.submit} close={this.close} />
    }
}

export class UserEdit extends Component {
    state = { error: {}, data: {} };

    componentDidMount() {
    if(this.props.location.state && this.props.location.state.user){
        this.setState({data: this.props.location.state.user})
    }else if(this.props.match.params.userId){
        axios.get(`/api/account/users/${this.props.match.params.userId}`).then(response => {
            this.setState({data: response.data});
        },error=>this.setState({error: (error.response ? error.response.data : error) }))
    }
    
  }

  submit = (data) => {
        return axios.put(`/api/account/users/${this.props.match.params.userId}`, data).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            error = error.response ? error.response.data : error;
            this.setState({error});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push('/school/account/users');
    }

    render() {
        return <AccountForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} close={this.close} />
    }
}