import React, { Component } from 'react';
import { inject, observer } from "mobx-react";
import lodash from 'lodash';
import $ from 'jquery';

import '../libs/bootstrap-datetimepicker'

import ShowNotification from '../components/ShowNotification';

@inject("userStore")
@observer
export default class Organization extends Component {
	defaults = {
		firstname: '',
		lastname: '',
		email: '',
		phonenumber: '',
		password_current: '',
		password: '',
		password_confirmation: ''
	};

	state = {error: {}, loading: false, data: {...this.defaults}};

	inputChanged = (event) => {
		let field = event.target.name;
		let val = event.target.value;
		this.props.userStore.user[field] = val;
	}

	submit = (data) => {
    return axios.put(`/api/account/users/${data.id}`, data).then( res => {
      this.setState({error: {}})
      return res;
    }, error => {
    	error = error.response ? error.response.data : error;
      this.setState({error});
      throw error;
    })
	}

	save = () => {
		let data = this.props.userStore.user;
		this.setState({loading: true});
		return this.submit(data).then( res => {
			ShowNotification("User Successfully Edited", "success", "nc-check-2");
		}, error => {
			error = error.response ? error.response.data : error;
			this.setState({error});
			return Promise.reject(error);
		}).finally(_=>this.setState({loading:false}));
	}

	render() {

		let error = this.state.error || {};
		let firstnameInvalid='', lastnameInvalid='', emailInvalid='',  passwordCurrentInvalid='', phonenumberInvalid='', passwordConfirmInvalid='', passwordInvalid='';
		if(error.errors) {
			firstnameInvalid = error.errors.firstname ? 'is-invalid' : '';
			lastnameInvalid = error.errors.lastname ? 'is-invalid' : '';
			emailInvalid = error.errors.email ? 'is-invalid' : '';
			phonenumberInvalid = error.errors.phonenumber ? 'is-invalid' : '';
			passwordCurrentInvalid = error.errors.password_current ? 'is-invalid' : '';
			passwordInvalid = error.errors.password ? 'is-invalid' : '';
			passwordConfirmInvalid = error.errors.password_confirmation ? 'is-invalid' : '';
		}

		return <div className="container-fluid" >
		  <div className="row">
		    <div className="col-md-12">
		      <div className="card stacked-form">
					  <div className="card-header ">
					    <h4 className="card-title">Account Details</h4>
					  </div>
					  <div className="card-body ">
					    <form method="#" action="#">
					    	{error.message && <div className="alert alert-danger">
                    <span>
                        <b> Error - </b> {error.message}
                    </span>
                </div>}
					      <div className="row">
					      	<div className="form-group col-md-6">
						        <label>Firstname</label>
						        <input value={this.props.userStore.user.firstname} onChange={this.inputChanged} name="firstname" type="text" placeholder="Firstname" className={`form-control ${firstnameInvalid}`} />
						        {firstnameInvalid && error.errors.firstname.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						      <div className="form-group col-md-6">
						        <label>Lastname</label>
						        <input value={this.props.userStore.user.lastname} onChange={this.inputChanged} name="lastname" type="text" placeholder="Lastname" className={`form-control ${lastnameInvalid}`} />
						        {lastnameInvalid && error.errors.lastname.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						    </div>

						    <div className="row">
					      	<div className="form-group col-md-6">
						        <label>email</label>
						        <input value={this.props.userStore.user.email} onChange={this.inputChanged} name="email" type="text" placeholder="Email" className={`form-control ${emailInvalid}`} />
						        {emailInvalid && error.errors.email.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						      <div className="form-group col-md-6">
						        <label>Phonenumber</label>
						        <input value={this.props.userStore.user.phonenumber} onChange={this.inputChanged} name="phonenumber" type="text" placeholder="Phonenumber" className={`form-control ${phonenumberInvalid}`} />
						        {phonenumberInvalid && error.errors.phonenumber.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						    </div>

						    <div className="form-group">
					        <label>Current Password</label>
					        <input value={this.props.userStore.user.password_current} onChange={this.inputChanged} name="password_current" type="password" placeholder="Enter Current Password" className={`form-control ${passwordCurrentInvalid}`} />
					        {passwordCurrentInvalid && error.errors.password_current.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>

						    <div className="form-group">
					        <label>Password</label>
					        <input value={this.props.userStore.user.password} onChange={this.inputChanged} name="password" type="password" placeholder="Enter Password" className={`form-control ${passwordInvalid}`} />
					        {passwordInvalid && error.errors.password.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>

					      <div className="form-group">
					        <label>Confirm Password</label>
					        <input value={this.props.userStore.user.password_confirmation} onChange={this.inputChanged} name="password_confirmation" type="password" placeholder="Re-enter Password" className={`form-control ${passwordConfirmInvalid}`} />
					        {passwordConfirmInvalid && error.errors.password_confirmation.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>


					    </form>
					  </div>
					  <div className="card-footer ">
					  	<div className="row">
			          <div className="col-sm-2">
			            <input onClick={this.save} type="button" value="SAVE" disabled={this.state.loading} className="btn btn-success" />
			          </div>
					    </div>
					  </div>

					</div>
		    </div>
		  </div>
		</div>

	}
}