import React, { Component } from 'react';
import lodash from 'lodash';

import ShowNotification from '../components/ShowNotification';
import confirmAction from '../utils/confirmAction';

export default class AccountForm extends Component {
	defaults = {
		firstname: '',
		lastname: '',
		email: '',
		phonenumber: ''
	};

	state = {error: {}, loading: false, data: {...this.defaults}};

	componentDidUpdate(prevProps,prevState) {
		if(this.props.edit){
			if(!lodash.isEqual(this.props.data, prevProps.data)){
				this.setState({data: {...this.state.data,...this.props.data}})
			}
		}
		if(!lodash.isEqual(this.props.error,prevProps.error)){
			this.setState({error: {...this.state.error,...this.props.error}})
		}
	}

	inputChanged = (event) => {
		let field = event.target.name;
		let val = event.target.value;
		this.setState( (state) => state.data[field] = val);
	}
	
	saveContinue = () => {
		this.submit().then(()=>{
			this.setState({data: {...this.defaults},error: {}});
		})
	}

	save = () => {
		this.submit().then(()=>this.props.close());
	}

	apply = () => {
		this.submit().then(()=>{
			this.setState({error: {}});
		})
	}

	submit = () => {
		let data = this.state.data;

		this.setState({loading: true});
		return this.props.submit(data).then( res => {
			if(this.props.edit) {
				ShowNotification("Account Successfully Edited", "success", "nc-check-2");
			}else {
				ShowNotification("Account Created Successfully", "success", "nc-check-2");
			}
		}, error => {
			this.setState({error});
			return Promise.reject(error);
		}).finally(_=>this.setState({loading:false}));
	}

	delete = () => {
		confirmAction( () => {
			return axios.delete(`/api/account/user/${this.props.data.id}`).then(res=>{
				this.props.close();
				return {success: 'Deleted!', succes_text: 'Account was deleted'};
			}, error => {
				throw {error: "Account couldn't be deleted."};
			})
		}, {loading_title: 'Deleting...', loading_text: 'Deleting Account', canceled: "Deleting Account Operation Cancelled", consequence: 'Account information will be lost'});
	}

	render() {
		let error = this.state.error || {};
		let firstnameInvalid = '', lastnameInvalid = '', emailInvalid = '', phonenumberInvalid = '', passwordCurrentInvalid='', passwordInvalid='', passwordConfirmInvalid='';
		if(error.errors) {
			firstnameInvalid = error.errors.firstname ? 'is-invalid' : '';
			lastnameInvalid = error.errors.lastname ? 'is-invalid' : '';
			emailInvalid = error.errors.email ? 'is-invalid' : '';
			phonenumberInvalid = error.errors.phonenumber ? 'is-invalid' : '';
			passwordCurrentInvalid = error.errors.password_current ? 'is-invalid' : '';
			passwordInvalid = error.errors.password ? 'is-invalid' : '';
			passwordConfirmInvalid = error.errors.password_confirmation ? 'is-invalid' : '';
		}

		return <div className="container-fluid" >
		  <div className="row">
		    <div className="col-md-12">
		      <div className="card stacked-form">
					  <div className="card-header ">
					    <h4 className="card-title">
					    	Account Details
					    	{ this.props.edit && <div className="pull-right">
					    			<button type="button" disabled={this.state.loading} className="btn btn-danger" onClick={this.delete} >Delete</button>
					    		</div>
					    	}
					    </h4>
					  </div>
					  <div className="card-body ">
					    <form method="#" action="#">
					    	{error.message && <div className="alert alert-danger">
                    <span>
                        <b> Error - </b> {error.message}
                    </span>
                </div>}
					      <div className="row">
					      	<div className="form-group col-md-6">
						        <label>Firstname</label>
						        <input value={this.state.data.firstname} onChange={this.inputChanged} name="firstname" type="text" placeholder="Firstname" className={`form-control ${firstnameInvalid}`} />
						        {firstnameInvalid && error.errors.firstname.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						      <div className="form-group col-md-6">
						        <label>Lastname</label>
						        <input value={this.state.data.lastname} onChange={this.inputChanged} name="lastname" type="text" placeholder="Lastname" className={`form-control ${lastnameInvalid}`} />
						        {lastnameInvalid && error.errors.lastname.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						    </div>

						    <div className="row">
					      	<div className="form-group col-md-6">
						        <label>email</label>
						        <input value={this.state.data.email} onChange={this.inputChanged} name="email" type="text" placeholder="Email" className={`form-control ${emailInvalid}`} />
						        {emailInvalid && error.errors.email.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						      <div className="form-group col-md-6">
						        <label>Phonenumber</label>
						        <input value={this.state.data.phonenumber} onChange={this.inputChanged} name="phonenumber" type="text" placeholder="Phonenumber" className={`form-control ${phonenumberInvalid}`} />
						        {phonenumberInvalid && error.errors.phonenumber.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						    </div>

						    {this.state.data.id && <div className="form-group">
					        <label>Current Password</label>
					        <input value={this.state.data.password_current} onChange={this.inputChanged} name="password_current" type="password" placeholder="Enter Current Password" className={`form-control ${passwordCurrentInvalid}`} />
					        {passwordCurrentInvalid && error.errors.password_current.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>}

						    <div className="form-group">
					        <label>Password</label>
					        <input value={this.state.data.password} onChange={this.inputChanged} name="password" type="password" placeholder="Enter Password" className={`form-control ${passwordInvalid}`} />
					        {passwordInvalid && error.errors.password.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>

					      <div className="form-group">
					        <label>Confirm Password</label>
					        <input value={this.state.data.password_confirmation} onChange={this.inputChanged} name="password_confirmation" type="password" placeholder="Re-enter Password" className={`form-control ${passwordConfirmInvalid}`} />
					        {passwordConfirmInvalid && error.errors.password_confirmation.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>


					    </form>
					  </div>
					  <div className="card-footer ">
					  	<div className="row">
			          <div className="col-sm-2">
			            <input onClick={this.save} type="button" value="SAVE" disabled={this.state.loading} className="btn btn-success" />
			          </div>
			          <div className="col-sm-2">
			            <input onClick={this.props.close} type="button" value="CLOSE" disabled={this.state.loading} className="btn btn-warning" />
			          </div>
					    </div>
					  </div>


					  


					</div>
		    </div>
		  </div>
		</div>

	}
}