import React, { Component } from 'react';
import lodash from 'lodash';
import $ from 'jquery';

import '../libs/bootstrap-datetimepicker'

import ShowNotification from '../components/ShowNotification';

export default class Organization extends Component {
	defaults = {
		name: '',
		telephone: '',
		address: '',
		county: '',
		location: '',
		motto: '',
		mission: ''
	};

	state = {previewUrl: '', error: {}, loading: false, classes: null, loading: false, data: {...this.defaults}};

	componentDidMount() {
    axios.get('/api/account/organization').then(res=>{
    	this.setState({data: {...this.defaults,...res.data}});
    });
	}

	componentDidUpdate(prevProps,prevState) {
		if(this.state.data.logo != prevState.data.logo){
			let url = '/uploads/organizations_logo/' + this.state.data.logo;
			this.setState({previewUrl: url});
		}
	}

	inputChanged = (field,event) => {
		let val = event.target.value;
		this.setState( (state) => state.data[field] = val);
	}

	profileChanged = (event) => {
		let files = event.target.files;
		if(files && files[0]) {
			var reader = new FileReader();
			reader.onload = (e) => {
				this.setState({previewUrl: e.target.result});
			};
			reader.readAsDataURL(files[0]);
			this.setState( (state) => state.data['logo'] = files[0])
		}
	}

	submit = (data) => {
		const formData = new FormData();
    formData.append('_method', 'PUT');
    for(let i in data){
      formData.append(i, data[i]);
    }
    return axios.post(`/api/account/organization`, formData, { headers: { 'content-type': 'multipart/form-data' } }).then( res => {
      this.setState({error: {}})
      return res;
    }, error => {
    	error = error.response ? error.response.data : error;
      this.setState({error});
      throw error;
    })
	}

	save = () => {
		let data = this.state.data;
		this.setState({loading: true});
		return this.submit(data).then( res => {
			ShowNotification("Organization Successfully Edited", "success", "nc-check-2");
		}, error => {
			error = error.response ? error.response.data : error;
			this.setState({error});
			return Promise.reject(error);
		}).finally(_=>this.setState({loading:false}));
	}

	render() {

		let error = this.state.error || {};
		let nameInvalid='', telephoneInvalid='', addressInvalid='', emailInvalid='', countyInvalid='', locationInvalid='', mottoInvalid='', missionInvalid='';
		if(error.errors) {
			nameInvalid = error.errors.name ? 'is-invalid' : '';
			telephoneInvalid = error.errors.telephone ? 'is-invalid' : '';
			addressInvalid = error.errors.address ? 'is-invalid' : '';
			emailInvalid = error.errors.email ? 'is-invalid' : '';
			countyInvalid = error.errors.county ? 'is-invalid' : '';
			locationInvalid = error.errors.location ? 'is-invalid' : '';
			mottoInvalid = error.errors.motto ? 'is-invalid' : '';
			missionInvalid = error.errors.mission ? 'is-invalid' : '';
		}

		return <div className="container-fluid" >
		  <div className="row">
		    <div className="col-md-12">
		      <div className="card stacked-form">
					  <div className="card-header ">
					    <h4 className="card-title">Organization Details</h4>
					  </div>
					  <div className="card-body ">
					    <form method="#" action="#">
					    	{error.message && <div className="alert alert-danger">
                    <span>
                        <b> Error - </b> {error.message}
                    </span>
                </div>}
					    	<div className="row">
					    		<div className="col-md-9">
					    				<div className="form-group">
								        <label>Organization Name</label>
								        <input value={this.state.data.name || ''} onChange={this.inputChanged.bind(this,'name')} type="text" placeholder="Organization Name" className={`form-control ${nameInvalid}`} />
								        {nameInvalid && error.errors.name.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
												<p className="help-block help-block-error"></p>
								      </div>

								      <div className="form-group">
								        <label>Telephone</label>
								        <input value={this.state.data.telephone || ''} onChange={this.inputChanged.bind(this,'telephone')} type="text" placeholder="Telephone" className={`form-control ${telephoneInvalid}`} />
								        {telephoneInvalid && error.errors.telephone.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
												<p className="help-block help-block-error"></p>
								      </div>

								      <div className="form-group">
								        <label>Address</label>
								        <input value={this.state.data.address || ''} onChange={this.inputChanged.bind(this,'address')} type="text" placeholder="Address" className={`form-control ${addressInvalid}`} />
								        {addressInvalid && error.errors.address.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
												<p className="help-block help-block-error"></p>
								      </div>
					    		</div>
						      <div className="col-md-3 d-flex align-items-center">
						      	<div>
						      	
						      	<div className="input-group">
						      		<div>
							      		<img src={this.state.previewUrl || '/img/profile-placeholder.jpg'} className="rounded img-fluid" style={{width: '100%', height: '100%'}} alt="Responsive image" />
							      	</div>
										  <span style={{position: 'absolute', bottom: 0}}>
										  	<span className="btn btn-primary"><i className="fa fa-camera"></i>Change</span>
										    <input type="file" onChange={this.profileChanged} style={{position: 'absolute', bottom: 0, top: 0, right: 0, left: 0}} className="custom-file-input" id="inputGroupFile01" aria-describedby="inputGroupFileAddon01" />
										  </span>
										</div>
										</div>
						      </div>
					      </div>

					      <div className="form-group">
					        <label>Email</label>
					        <input value={this.state.data.email || ''} onChange={this.inputChanged.bind(this,'email')} type="text" placeholder="Email" className={`form-control ${emailInvalid}`} />
					        {emailInvalid && error.errors.email.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>

					      <div className="row">
					      	
					      	<div className="form-group col-md-6">
						        <label>Count/State</label>
						        <input value={this.state.data.county || ''} onChange={this.inputChanged.bind(this,'county')} type="text" placeholder="County" className={`form-control ${countyInvalid}`} />
						        {countyInvalid && error.errors.county.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						      <div className="form-group col-md-6">
						        <label>Location</label>
						        <input value={this.state.data.location || ''} onChange={this.inputChanged.bind(this,'location')} type="text" placeholder="Location" className={`form-control ${locationInvalid}`} />
						        {locationInvalid && error.errors.location.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						      
						    </div>

						    <div className="form-group">
					        <label>Motto</label>
					        <input value={this.state.data.motto || ''} onChange={this.inputChanged.bind(this,'motto')} type="text" placeholder="Motto" className={`form-control ${mottoInvalid}`} />
					        {mottoInvalid && error.errors.motto.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>

					      <div className="form-group">
					        <label>Mission</label>
					        <input value={this.state.data.mission || ''} onChange={this.inputChanged.bind(this,'mission')} type="text" placeholder="Mission" className={`form-control ${missionInvalid}`} />
					        {missionInvalid && error.errors.mission.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>


					    </form>
					  </div>
					  <div className="card-footer ">
					  	<div className="row">
			          <div className="col-sm-2">
			            <input onClick={this.save} type="button" value="SAVE" disabled={this.state.loading} className="btn btn-success" />
			          </div>
					    </div>
					  </div>


					  


					</div>
		    </div>
		  </div>
		</div>

	}
}