import React, { Component } from 'react';
import { Route } from "react-router-dom";

import AnimatedSwitch from '../components/animated-switch';

import Organization from './Organization';
import Account from './Account';
import Users, {UserNew, UserEdit} from './Users';

export default class StudentPages extends Component {
	render() {
		let match = this.props.match;
		return <AnimatedSwitch>
						<Route path={`${match.path}/organization`} render={(props)=> <Organization {...props} /> } />
						<Route path={`${match.path}/users/new`} render={(props)=> <UserNew {...props} /> } />
						<Route path={`${match.path}/users/:userId`} render={(props)=> <UserEdit {...props} /> } />
						<Route path={`${match.path}/users`} render={(props)=> <Users {...props} /> } />
						<Route path={`${match.path}`} render={(props)=> <Account {...props} /> } />
					</AnimatedSwitch>
	}
}